<template id="">
  <div class="">

    <!-- <div @click='closeWindow' class="window">
    </div> -->

    <div class="windowContent">
      <div class="Content">
        <input class='titileInput' id="titile" type="text" name="" value="" placeholder="Название стикера">
        <p class='description'>Какие бизнес-задачи стоят перед клиентом</p>
        <textarea id="textarea1" name="name" rows="2" cols="60"></textarea>
        <p class='description' >В чем клиент хочет улучшить свой сервис/продукт</p>
        <textarea id="textarea2" name="name" rows="2" cols="60"></textarea>
        <p class='description' >Что наиболее ценно для клиента с точки зрения денег, времени и усилий</p>
        <textarea id="textarea3" name="name" rows="8" cols="60"></textarea>
        <button @click='SaveCard' type="button" class='btn' name="button">Сохранить</button>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery';

export default {

  name: 'window',
  props: {
  },

  mounted(){

    const vm = this;

    $(document).mouseup(function(e) {

      var block = $(".Content");

      if (!block.is(e.target) && block.has(e.target).length === 0) {

        vm.$emit('closeWindow')
      } else {
        // this.$emit('closeWindow')

      }
    });
  },
  methods: {
    SaveCard(){

      this.$emit('SaveCard',{'name':'ClientTasks', 'data': [$('#titile').val(),$('#textarea1').val(),$('#textarea2').val(),$('#textarea3').val()]})

    }
  }

}

</script>

