<template id="">
  <div class="">

    <!-- <div @click='closeWindow' class="window">
    </div> -->

    <div class="windowContent">
      <div class="Content">
        <!-- <input class='titileInput' id="titile" type="text" name="" value="" placeholder="Название стикера"> -->
        <p class='description' >Заметки </p>
        <textarea v-model="ForNotesLocal" id="textarea2" name="name" rows="22" cols="60"></textarea>
        <button @click='SaveCard' type="button" class='btn' name="button">Сохранить</button>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery';

export default {

  name: 'window',
  props: {
    ForNotes: {required: true}
  },
  data(){
    return{
        ForNotesLocal: '',
       }
   },
  mounted(){
    this.ForNotesLocal = this.ForNotes



    const vm = this;

    $(document).mouseup(function(e) {

      var block = $(".Content");

      if (!block.is(e.target) && block.has(e.target).length === 0) {

        vm.$emit('closeWindow')
      } else {
        // this.$emit('closeWindow')

      }
    });
  },
  methods: {
    SaveCard(){
      let data = this.ForNotesLocal;
      this.$emit('SaveNotes',data)
      this.$emit('closeWindow')

    }
  }

}

</script>

